@import url(https://fonts.googleapis.com/css?family=Roboto:300,400);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  height: 100%;
  background-color: #c2cfdc;
}

.Navbar {
  -webkit-touch-callout: none;
  /* iOS Safari */
  -webkit-user-select: none;
  /* Safari */
  /* Konqueror HTML */
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer/Edge */
  user-select: none;
  /* Non-prefixed version, currently
     supported by Chrome and Opera */
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  font-weight: 300;
  font-size: 20px;
  background-color: #2e3943;
}
.Navbar ul {
  text-align: center;
  list-style-type: none;
  max-width: 700px;
  margin: 0 auto;
  padding: 0;
}
.Navbar ul #selected {
  color: white;
  border-bottom: 2px solid #abc4dc;
}
.Navbar ul li {
  color: #abc4dc;
  padding: 10px 35px 10px 35px;
  min-width: 20px;
  margin-left: 20px;
  display: inline-block;
  cursor: pointer;
}
.Navbar ul li:hover {
  color: white;
}
.Navbar ul li:first-child {
  margin: 0 !important;
}

.Home #header {
  margin: 0;
  background: #2e3943;
  background: -webkit-linear-gradient(bottom, #2e3943 0%, #5c6976 50%);
  background: linear-gradient(0deg, #2e3943 0%, #5c6976 50%);
  width: 100%;
  height: 450px;
}
.Home #header #blurb {
  text-align: center;
  color: white;
  max-width: 550px;
  margin: 0 auto;
  padding-top: 60px;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  font-weight: 300;
  font-size: 40px;
}
.Home #header #blurb #name {
  border-bottom: 2px solid #abc4dc;
  padding-bottom: 30px;
  margin-bottom: 30px;
}
.Home #header p {
  margin: 0;
}
.Home #intro {
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  font-weight: 300;
  max-width: 700px;
  font-size: 25px;
  margin: 0 auto;
}
.Home button {
  margin-top: 35px;
  margin-left: 15px;
  margin-right: 15px;
  border: 2px solid #4e78a2;
  border-radius: 15px;
  color: #c2cfdc;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  font-weight: 350;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 20px;
  background-color: #4e78a2;
  cursor: pointer;
}
.Home button:first-child {
  margin-right: 10px;
}
.Home button:hover {
  color: white;
  border: 2px solid #abc4dc;
}

.About {
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  max-width: 550px;
  margin: 0 auto;
}
.About h3 {
  font-weight: 300;
  font-size: 30px;
  text-align: center;
}
.About p {
  font-weight: 350;
}

